<svelte:head>
	<link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootswatch/4.5.0/flatly/bootstrap.min.css">
</svelte:head>
<style>
.login{
  min-height: 100vh;
}
	.textBlue {
		color: #5778f3;
	}
</style>
<script>
import {Row, Col, Container, Jumbotron, Form, FormGroup, FormText, Input, CustomInput, Label, Button, Navbar, NavbarBrand, Nav, NavItem} from 'sveltestrap';
import './firebase.js'
import Profile from './Profile.svelte';
import Footer from './Footer.svelte';
import * as firebaseui from 'firebaseui/dist/npm';

	//yaha pe auth = firebase.auth()

  import { auth, googleProvider, twitterProvider, firebaseAnalytics } from './firebase';
    import { authState } from 'rxfire/auth';

    let user;

	let ui = new firebaseui.auth.AuthUI(auth);
    const unsubscribe = authState(auth).subscribe(u => user = u);

    function googleLogin() {
		auth.signInWithPopup(googleProvider);
		firebaseAnalytics.logEvent('google_login');
	}

	function twitterLogin(){
		
		auth.signInWithRedirect(twitterProvider);

		auth.getRedirectResult().then(function(result) {
		if (result.credential) {
			// This gives you a the Twitter OAuth 1.0 Access Token and Secret.
			// You can use these server side with your app's credentials to access the Twitter API.
			var token = result.credential.accessToken;
			var secret = result.credential.secret;
			// ...
		}
		// The signed-in user info.
			user = auth.currentUser;

		if (user != null) {
		user.providerData.forEach(function (profile) {
		/*	console.log("Sign-in provider: " + profile.providerId);
			console.log("  Provider-specific UID: " + profile.uid);
			console.log("  Name: " + profile.displayName);
			console.log("  Email: " + profile.email);
			console.log("  Photo URL: " + profile.photoURL);*/
		});
		}
		}).catch(function(error) {
		// Handle Errors here.
		var errorCode = error.code;
		var errorMessage = error.message;
		// The email of the user's account used.
		var email = error.email;
		// The firebase.auth.AuthCredential type that was used.
		var credential = error.credential;
		// ...
		});
		firebaseAnalytics.logEvent('twitter_login');
	}

	function emailLogin() {
		/*ui.start('#firebaseui-auth-container', {
		signInOptions: [
		firebase.auth.EmailAuthProvider.PROVIDER_ID
		],
		// Other config options...
		});*/

		let uiConfig = {
			callbacks: {
				signInSuccessWithAuthResult: function(authResult, redirectUrl) {
				// User successfully signed in.
				// Return type determines whether we continue the redirect automatically
				// or whether we leave that to developer to handle.
				user.sendEmailVerification();
				return true;
				},
				uiShown: function() {
				// The widget is rendered.
				// Hide the loader.
				document.getElementById('loader').style.display = 'none';
				}
				},
				// Will use popup for IDP Providers sign-in flow instead of the default, redirect.
				signInFlow: 'popup',
				signInOptions: [
				firebase.auth.EmailAuthProvider.PROVIDER_ID,
				],
				// Terms of service url.
				tosUrl: '/TOS',
				// Privacy policy url.
				privacyPolicyUrl: '/PrivacyPolicy'
			};

	// The start method will wait until the DOM is loaded.
	ui.start('#firebaseui-auth-container', uiConfig);
	firebaseAnalytics.logEvent('email_login');
	}
console.log(auth.currentUser)
</script>
<Container fluid>
	{#if user} 
		<Profile {...user} />
	{:else}
	<Row class="login text-center py-5">
								<Col sm="12" class="m-2">
										<p class="textBlue display-4 font-weight-bold">Sign in to your account</p>
								</Col>
								<Col sm="12">
									<div class="m-4">
									<Row>
										<Col>
											<h5>üí™ Build and log content creation habit.</h5>
										</Col>
										<Col>
											<h5>‚è≥ Track how much time is being saved.</h5>
										</Col>
										<Col>
											<h5>üé® Start making social media content.</h5>
										</Col>
									</Row>
									</div>
								</Col>
								<Col sm="12" class="text-center">
									<Button color="primary" class="w-25 font-weight-bold mb-2 shadow-sm" on:click={emailLogin}>
										Email
									</Button>
									<div id="firebaseui-auth-container"></div>
									<div id="loader" hidden><h5>Loading...</h5></div>
								</Col>
                                <Col sm="12" class="text-center">
									<Button color="light" class="w-25 font-weight-bold mb-2 shadow-sm" on:click={googleLogin}>
										Google
									</Button>
								</Col>
								<Col sm="12" class="text-center">
									<Button color="info" class="w-25 font-weight-bold mb-2 shadow-sm" on:click={twitterLogin}>
										Twitter
									</Button>
								</Col>
	</Row>
	{/if}
	<hr/>
	<Footer />
</Container>