<svelte:head>
  <link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootswatch/4.5.0/flatly/bootstrap.min.css">
</svelte:head>
<style>
  @font-face {
    font-family: "impactFont";
    src: url("../fonts/impact.ttf");
  }

  #stage-parent {
    max-width: 720px;
    width: 100%;
    border: 1px black solid;
  }

  .textBlue {
    color: #5778f3;
  }

  .pressDown {
    background-color: #5778f3;
    padding: 10px 30px;
    color: #fff;
    border-radius: 5px;
    border-bottom: 4px solid #3e4caf;
    transition: 0.1s;
  }

    .pressDownDisabled{
        background-color: #5a5b63;
        padding: 10px 30px;
        color: #fff;
        border-radius: 5px;
        border-bottom: 4px solid #cfcece;
        transition: 0.1s;
    }

  .pressDown:hover {
    border-bottom-width: 0;
    margin-top: 3px;
    color: #fff;
  }

  .pressDownDefault {
    background-color: #707070;
    padding: 10px 30px;
    color: #fff;
    border-radius: 5px;
    border-bottom: 4px solid #35363d;
    transition: 0.1s;
  }

  .pressDownDefault:hover {
    border-bottom-width: 0;
    margin-top: 3px;
    color: #fff;
  }
</style>
<script>
  import {
    Row,
    Col,
    Container,
    Jumbotron,
    Label,
    Input,
    Button,
    Navbar,
    NavbarBrand,
    Nav,
    NavItem
  } from 'sveltestrap'
  import {
    Router,
    Link,
    Route
  } from "svelte-routing";

  import Footer from '../Footer.svelte';
  import NotFound from '../NotFound.svelte';

  import {
    FiveBlobs
  } from '../modules/Blob.svelte';
  import {
    colorPalette
  } from '../modules/Color.svelte';
  let svgPaths = FiveBlobs();
  let colorArr = colorPalette();

  import Konva from 'konva';
  import {
    onMount
  } from 'svelte';
  import {
    beforeUpdate,
    afterUpdate
  } from 'svelte';

  /*
  Authentication check procedure:-
  1. When user logged in, It's uid from auth.currentuser of firebase is stored in a localStorage 
  2. Now, that uid is enough for validation everywhere and i can access localstorage anywhere.
  3. thus, uid is the only thing needed after user is logged in, which can be retrieved with localstorage.getitem and its key 'usrkey'
  4. on logging out, localstorage is cleared.
  */
  import {
    auth,
    db,
    firebaseAnalytics
  } from '../firebase';
  let authinfo = localStorage.getItem('usrkey');
  let user = JSON.parse(authinfo);

let allowProAccess = false;

function proAccessCheck() {
db.collection('PROusers').doc(user).get().then(doc => {
      if (doc.exists) {
      console.log("Pro Access ENABLED :)");
      allowProAccess = true;
  } else {
      // doc.data() will be undefined in this case
      console.log("Pro Access Disabled");
      allowProAccess = false;
  }
  }).catch(function(error) {
      console.log("Error:", error);
  });
}
  let flag = 0;
  let user_text, website_name;
  let stage, width = 720,
    height = 350;
  let textNode = new Konva.Text();
  let layer = new Konva.Layer();
  let bglayer = new Konva.Layer();
  let shapelayer = new Konva.Layer();

  let stageBox;

  let isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);

  //code to check if user downloaded and then increment.
  let timesave, contentMadeSoFar = 0;

  function quoteIcon() {
    //add a rough filter here, an actual image overlay.
    for (let i = 0; i < 1; i++) {

      let url2 =
        '<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="128" height="128"><path fill="none" d="M0 0h24v24H0z"/><path fill="' +
        colorArr[i + 3] +
        '" d="M19.417 6.679C20.447 7.773 21 9 21 10.989c0 3.5-2.457 6.637-6.03 8.188l-.893-1.378c3.335-1.804 3.987-4.145 4.247-5.621-.537.278-1.24.375-1.929.311-1.804-.167-3.226-1.648-3.226-3.489a3.5 3.5 0 0 1 3.5-3.5c1.073 0 2.099.49 2.748 1.179zm-10 0C10.447 7.773 11 9 11 10.989c0 3.5-2.457 6.637-6.03 8.188l-.893-1.378c3.335-1.804 3.987-4.145 4.247-5.621-.537.278-1.24.375-1.929.311C4.591 12.322 3.17 10.841 3.17 9a3.5 3.5 0 0 1 3.5-3.5c1.073 0 2.099.49 2.748 1.179z"/></svg>';
      let url = 'data:image/svg+xml;base64,' + window.btoa(url2);

      Konva.Image.fromURL(url, image => {
        image.draggable(true);
        image.x(550);
        image.y(returnRandomY() + 15);
        shapelayer.add(image);
        shapelayer.draw();
      })
    }
  }

  function statsCounter() {
    db.collection('usersDB').doc(user.uid).get().then(doc => {
      if (doc.exists) {
        //alert("stat counter if statement me aya hu")
        //if user is already there, then add +1
        let val = contentMadeSoFar + 1
        db.collection('usersDB').doc(user.uid).set({
          totalCreated: val
        })
        contentMadeSoFar = val
        timesaved()
      } else {
        // alert("stat counter else statement me aya hu")
        db.collection('usersDB').doc(user.uid).set({
          totalCreated: val
        })
        contentMadeSoFar = val
        timesaved()
      }
    }).catch(function (error) {
      console.log("Error getting document:", error);
    });
  }

  function remix() {
    //stage.destroyChildren();
    bglayer.destroy();
    shapelayer.destroy();
    //layer = new Konva.Layer();
    bglayer = new Konva.Layer();
    shapelayer = new Konva.Layer();

    bglayer.moveToBottom();
    stage.add(bglayer);
    stage.add(shapelayer);
    layer.moveToTop();
    //stage.add(layer);

    svgPaths = FiveBlobs();
    colorArr = colorPalette();

    addBackground();
    quoteIcon();
  }

  function loadCustomFont() {
    // FONT LOADING DETECTION CODE:
    var canvas = document.createElement('canvas');
    var ctx = canvas.getContext('2d');
    ctx.font = 'normal 20px CustomFont';

    var isFontLoaded = false;
    var TEXT_TEXT = 'Some test text;';
    var initialMeasure = ctx.measureText(TEXT_TEXT);
    var initialWidth = initialMeasure.width;

    // here is how the function works
    // different fontFamily may have different width of symbols
    // when font is not loaded a browser will use startard font as a fallback
    // probably Arial
    // when font is loaded measureText will return another width
    function whenFontIsLoaded(callback, attemptCount) {
      if (attemptCount === undefined) {
        attemptCount = 0;
      }
      if (attemptCount >= 20) {
        callback();
        return;
      }
      if (isFontLoaded) {
        callback();
        return;
      }
      const metrics = ctx.measureText(TEXT_TEXT);
      const width = metrics.width;
      if (width !== initialWidth) {
        isFontLoaded = true;
        callback();
      } else {
        setTimeout(function () {
          whenFontIsLoaded(callback, attemptCount + 1);
        }, 1000);
      }
    }

    whenFontIsLoaded(function () {
      // set font style when font is loaded
      // so Konva will recalculate text wrapping if it has limited width
      textNode.fontFamily('impactFont');
      layer.draw();
    });
  }

  function subHeadingText() {
    //The minor heading text
    var minortextNode = new Konva.Text({
      text: '@yourusername',
      x: 20,
      y: 20,
      fontSize: 20,
      fill: 'white',
    });
    minortextNode.width(500)
    layer.add(minortextNode);

    layer.draw();

    minortextNode.on('dblclick', () => {
      // create textarea over canvas with absolute position

      // first we need to find position for textarea
      // how to find it?

      // at first lets find position of text node relative to the stage:
      var textPosition = minortextNode.getAbsolutePosition();
      console.log(textPosition);

      // then lets find position of stage container on the page:
      var stageBox = stage.container().getBoundingClientRect();
      console.log(stageBox);

      // so position of textarea will be the sum of positions above:
      var areaPosition = {
        x: width + textPosition.x,
        y: height + textPosition.y
      };

      // create textarea and style it
      var textarea = document.createElement('textarea');
      document.body.appendChild(textarea);

      textarea.value = minortextNode.text();
      textarea.style.position = 'absolute';
      textarea.style.rows = 10;
      textarea.style.cols = 50;
      textarea.style.top = areaPosition.y + 'px';
      textarea.style.left = (areaPosition.x - 300) + 'px';
      textarea.style.width = minortextNode.width();

      textarea.focus();

      textarea.addEventListener('keydown', function (e) {
        // hide on enter
        if (e.keyCode === 13) {
          minortextNode.text(textarea.value);
          layer.draw();
          document.body.removeChild(textarea);
        }
      });
    });
  }

  function headingText() {
    loadCustomFont();
    textNode = new Konva.Text({
      text: '5 Ways to Actually Become Smarter!',
      x: 150,
      y: 100,
      fontSize: 65,
      fontFamily: 'impactFont',
      fontVariant: 'small-caps',
      stroke: 'black',
      strokeWidth: 2,
      fill: 'white',
      align: 'left',
      draggable: 'true',
      wrap: 'word',
      shadowEnabled: 'true',
      shadowColor: 'black',
      shadowOpacity: 0.8,
      shadowBlur: 4,
      shadowOffsetX: 3,
      shadowOffsetY: 3
    });
    textNode.width(400)
    layer.add(textNode);
    layer.draw();

    textNode.on('dblclick', () => {
      // create textarea over canvas with absolute position

      // first we need to find position for textarea
      // how to find it?

      // at first lets find position of text node relative to the stage:
      var textPosition = textNode.getAbsolutePosition();
      console.log(textPosition);

      // then lets find position of stage container on the page:
      var stageBox = stage.container().getBoundingClientRect();
      console.log(stageBox);

      // so position of textarea will be the sum of positions above:
      var areaPosition = {
        x: width + textPosition.x,
        y: height + textPosition.y
      };

      // create textarea and style it
      var textarea = document.createElement('textarea');
      document.body.appendChild(textarea);

      textarea.value = textNode.text();
      textarea.style.position = 'absolute';
      textarea.style.rows = 10;
      textarea.style.cols = 50;
      textarea.style.top = areaPosition.y + 'px';
      textarea.style.left = (areaPosition.x - 300) + 'px';
      textarea.style.width = textNode.width();

      textarea.focus();

      textarea.addEventListener('keydown', function (e) {
        // hide on enter
        if (e.keyCode === 13) {
          textNode.text(textarea.value);
          layer.draw();
          document.body.removeChild(textarea);
        }
      });
    });
  }

  function addBackground() {
    //this code below is to apply random background.
    let bgRect = new Konva.Rect({
      x: 0,
      y: 0,
      width: width,
      height: height,
      fill: colorArr[0],
      draggable: false,
    });
    bglayer.add(bgRect)
    bglayer.draw();
  }

  function returnRandom() {
    return Math.floor(Math.random() * 300) + 50;
  }

  function returnRandomY() {
    return Math.floor(Math.random() * 100) + 50;
  }

  function forTwitter() {
    if (isMobile) {
      var dataURL = stage.toDataURL({
        pixelRatio: 2
      });
      downloadURI(dataURL, 'posterTW.png');
    } else {
      var dataURL = stage.toDataURL({
        pixelRatio: 1
      });
      downloadURI(dataURL, 'posterTW.png');
    }
    statsCounter();

  }

  function forFacebook() {
    if (isMobile) {
      var dataURL = stage.toDataURL({
        pixelRatio: 4
      });
      downloadURI(dataURL, 'posterFB.png');
    } else {
      var dataURL = stage.toDataURL({
        pixelRatio: 2
      });
      downloadURI(dataURL, 'posterFB.png');
    }
    statsCounter();

  }

  function forLinkedIn() {
    if (isMobile) {
      var dataURL = stage.toDataURL({
        pixelRatio: 4
      });
      downloadURI(dataURL, 'posterLN.png');
    } else {
      var dataURL = stage.toDataURL({
        pixelRatio: 2
      });
      downloadURI(dataURL, 'posterLN.png');
    }
    statsCounter();
  }

  function downloadURI(uri, name) {

    let link = document.createElement('a');
    link.download = name;
    link.href = uri;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }

  beforeUpdate(() => {
    console.log("beforeUpdate was called");
    //setting default values to canvas dimensions
    width = 720;
    height = 350;
    // then create default one layer
    //layer = new Konva.Layer();
  });

  afterUpdate(() => {
    function fitStageIntoParentContainer() {
      let lcontainer = document.getElementById('stage-parent');

      // now we need to fit stage into parent
      var containerWidth = document.getElementById('stage-parent').offsetWidth;
      // to do this we need to scale the stage
      var scale = containerWidth / width;

      stage.width(width * scale);
      stage.height(height * scale);
      stage.scale({
        x: scale,
        y: scale
      });
      stage.draw();
    }

    fitStageIntoParentContainer();
    // adapt the stage on any window resize
    window.addEventListener('resize', fitStageIntoParentContainer);
  })
  onMount(() => {
        proAccessCheck();
    firebaseAnalytics.logEvent('Quotes_2_Template');
    //code to intialize counter for stats
    db.collection('usersDB').doc(user).get().then(doc => {
      if (doc.exists) {
        let userData = doc.data();
        contentMadeSoFar = userData.totalCreated;
      } else {
        db.collection('usersDB').doc(user).set({
          totalCreated: 0
        })
      }
    }).catch(function (error) {
      console.log("Error getting document:", error);
    });

    // first we need to create a stage
    stage = new Konva.Stage({
      container: 'konva-stage', // id of container <div>
      width: 720,
      height: 350
    });

    //find position of stage container on the page:
    stageBox = stage.container().getBoundingClientRect();

    layer = new Konva.Layer();
    bglayer = new Konva.Layer();
    shapelayer = new Konva.Layer();

    bglayer.moveToBottom();
    shapelayer.moveToTop();
    stage.add(bglayer);
    stage.add(shapelayer);
    stage.add(layer);

    addBackground();
    headingText();
    subHeadingText();
    quoteIcon();
  });
</script>
<div>
  <Container fluid>
    {#if user} 
	<Row class="py-5">
		<Col sm="12" class="p-3">
			<h1 class="textBlue gradient-text font-weight-bold text-center">Quote Template #3</h1>
		</Col>
    <Col sm="12" class="text-center">
            <h4 class="font-weight-bold">Edit with your text. You can also move the main text.</h4>
    </Col>
    
    </Row>

    <Row>
      <Col class="d-flex justify-content-center">
        <div id="stage-parent">
          <div id="konva-stage"></div>
        </div>
      </Col>
    </Row>

        <Row class="mt-2 justify-content-center">
        <div>
            <Row class="pt-3">
              {#if !allowProAccess}
              <Col sm="12" class="text-center">
                  <h5 class="textBlue font-weight-bold">‚ö† This feature requires PRO account.</h5>
                  <button id="toggleForPro" class="pressDownDisabled btn-block btn-lg" disabled>Remix Design</button>
              </Col>
              {:else}
              <Col sm="12" class="text-center">
                <button id="toggleForPro" class="pressDown btn-block btn-lg" on:click={remix}>Remix Design</button>
              </Col>
              {/if}
            </Row>
                <div>
                    <Row class="pt-5">
                        <Col sm="12" class="text-center">
                          <h4 class="textBlue font-weight-bold">Download For Different Social Media.</h4>
                        </Col>
                    </Row>
                    <Row class="mt-2 mx-4 text-center">
                    <Col>
                    <button class="pressDown btn-block btn-lg" on:click={forFacebook}>Facebook</button>
                    </Col>
                    <Col>
                    <button class="pressDown btn-block btn-lg" on:click={forTwitter}>Twitter</button>
                    </Col>
                    <Col>
                    <button class="pressDown btn-block btn-lg" on:click={forLinkedIn}>Linkedin</button>
                    </Col>
                </Row>
                </div>
        </div>
        </Row>
{:else}
<NotFound />
{/if}
<hr/>
<Footer />
</Container>
</div>