    max-width: 720px;
    width: 100%;
    border: 1px black solid;
  }

    .textBlue{
        color: #5778f3;
    }
    .pressDown {
        background-color: #5778f3;
        padding: 10px 30px;
        color: #fff;
        border-radius: 5px;
        border-bottom: 4px solid #3e4caf;
        transition: 0.1s;
    }

    .pressDownDisabled{
        background-color: #5a5b63;
        padding: 10px 30px;
        color: #fff;
        border-radius: 5px;
        border-bottom: 4px solid #cfcece;
        transition: 0.1s;
    }

    .pressDown:hover{
        border-bottom-width: 0;
        margin-top: 3px;
        color: #fff;
    }

    .pressDownDefault {
        background-color: #707070;
        padding: 10px 30px;
        color: #fff;
        border-radius: 5px;
        border-bottom: 4px solid #35363d;
        transition: 0.1s;
    }

    .pressDownDefault:hover{
        border-bottom-width: 0;
        margin-top: 3px;
        color: #fff;
    }
</style>
<script>
  import {Row, Col, Container, Jumbotron, Label, Input, Button, Navbar, NavbarBrand, Nav, NavItem} from 'sveltestrap'
  import { Router, Link, Route } from "svelte-routing";

  import Footer from '../Footer.svelte';
  import NotFound from '../NotFound.svelte';

  import { FiveBlobs } from '../modules/Blob.svelte';
  import {colorPalette} from '../modules/Color.svelte';
  let svgPaths = FiveBlobs();
  let colorArr = colorPalette();

  import Konva from 'konva';
  import { onMount } from 'svelte';
  import { beforeUpdate, afterUpdate } from 'svelte';
 
/*
Authentication check procedure:-
1. When user logged in, It's uid from auth.currentuser of firebase is stored in a localStorage 
2. Now, that uid is enough for validation everywhere and i can access localstorage anywhere.
3. thus, uid is the only thing needed after user is logged in, which can be retrieved with localstorage.getitem and its key 'usrkey'
4. on logging out, localstorage is cleared.
*/
import { auth, db, firebaseAnalytics } from '../firebase';
let authinfo = localStorage.getItem('usrkey');
let user = JSON.parse(authinfo);

let allowProAccess = false;

function proAccessCheck() {
db.collection('PROusers').doc(user).get().then(doc => {
      if (doc.exists) {
      console.log("Pro Access ENABLED :)");
      allowProAccess = true;
  } else {
      // doc.data() will be undefined in this case
      console.log("Pro Access Disabled");
      allowProAccess = false;
  }
  }).catch(function(error) {
      console.log("Error:", error);
  });
}
  let user_text, website_name;
  let stage, width=720,height=350;
  
  let layer = new Konva.Layer();
  let bglayer = new Konva.Layer();
  let shapelayer = new Konva.Layer();
  
  let isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
  
//code to check if user downloaded and then increment.
let timesave, contentMadeSoFar=0;

function addFooterBar(){
    let txtBar = new Konva.Rect({
            x:0,y:325,
            width: '1000',
            height: '30',
            fill: '#262626',
            draggable: false,
        });
			
        bglayer.add(txtBar)
        bglayer.draw();
}

function statsCounter(){
  db.collection('usersDB').doc(user).get().then(doc => {
      if (doc.exists) {
          //alert("stat counter if statement me aya hu")
          //if user is already there, then add +1
              let val = contentMadeSoFar+1
              db.collection('usersDB').doc(user).set({
                      totalCreated: val
              })
              contentMadeSoFar = val
              timesaved()
      } else {
         // alert("stat counter else statement me aya hu")
          db.collection('usersDB').doc(user).set({
                      totalCreated: val
              })
              contentMadeSoFar = val
              timesaved()
      }
      }).catch(function(error) {
          console.log("Error getting document:", error);
      });
}

function shuffle() {
        //stage.destroyChildren();
bglayer.destroy();
shapelayer.destroy();
        //layer = new Konva.Layer();
        bglayer = new Konva.Layer();
        shapelayer = new Konva.Layer();

        bglayer.moveToBottom();
        stage.add(bglayer);
        stage.add(shapelayer);
        layer.moveToTop();
        //stage.add(layer);
  
        svgPaths = FiveBlobs();
        colorArr = colorPalette();
        
        addBackground()
        addFooterBar();
        addShapes();
        //addText();
}

function addText(){
    //The main heading text
    var textNode = new Konva.Text({
        text: 'This is the heading. Edit me!',
        x: 100,
        y: 100,
        fontSize: 40,
        fill: 'white'
      });
      textNode.width(500)
      layer.add(textNode);
      //layer.draw();

      textNode.on('dblclick', () => {
        // create textarea over canvas with absolute position

        // first we need to find position for textarea
        // how to find it?

        // at first lets find position of text node relative to the stage:
        var textPosition = textNode.getAbsolutePosition();
        console.log(textPosition);

        // then lets find position of stage container on the page:
        var stageBox = stage.container().getBoundingClientRect();
        console.log(stageBox);

        // so position of textarea will be the sum of positions above:
        var areaPosition = {
          x: width + textPosition.x,
          y: height + textPosition.y
        };

        // create textarea and style it
        var textarea = document.createElement('textarea');
        document.body.appendChild(textarea);

        textarea.value = textNode.text();
        textarea.style.position = 'absolute';
        textarea.style.rows = 10;
        textarea.style.cols = 50;
        textarea.style.top = areaPosition.y + 'px';
        textarea.style.left = (areaPosition.x - 300) + 'px';
        textarea.style.width = textNode.width();

        textarea.focus();

        textarea.addEventListener('keydown', function (e) {
          // hide on enter
          if (e.keyCode === 13) {
            textNode.text(textarea.value);
            layer.draw();
            document.body.removeChild(textarea);
          }
        });
      });

      //The minor heading text
    var minortextNode = new Konva.Text({
        text: 'This is the heading. Edit me!',
        x: 100,
        y: 250,
        fontSize: 20,
        fill: 'white'
      });
      minortextNode.width(500)
      layer.add(minortextNode);
      //layer.draw();

      minortextNode.on('dblclick', () => {
        // create textarea over canvas with absolute position

        // first we need to find position for textarea
        // how to find it?

        // at first lets find position of text node relative to the stage:
        var textPosition = minortextNode.getAbsolutePosition();
        console.log(textPosition);

        // then lets find position of stage container on the page:
        var stageBox = stage.container().getBoundingClientRect();
        console.log(stageBox);

        // so position of textarea will be the sum of positions above:
        var areaPosition = {
          x: width + textPosition.x,
          y: height + textPosition.y
        };

        // create textarea and style it
        var textarea = document.createElement('textarea');
        document.body.appendChild(textarea);

        textarea.value = textNode.text();
        textarea.style.position = 'absolute';
        textarea.style.rows = 10;
        textarea.style.cols = 50;
        textarea.style.top = areaPosition.y + 'px';
        textarea.style.left = (areaPosition.x - 300) + 'px';
        textarea.style.width = textNode.width();

        textarea.focus();

        textarea.addEventListener('keydown', function (e) {
          // hide on enter
          if (e.keyCode === 13) {
            minortextNode.text(textarea.value);
            layer.draw();
            document.body.removeChild(textarea);
          }
        });
      });

      //the footer bottom text
      var subHeadingNode = new Konva.Text({
        text: 'Edit this text by double clicking.',
        x: 240,
        y: 330,
        fontSize: 15,
        fill: 'white'
      });
      subHeadingNode.width(500)
      layer.add(subHeadingNode);
      layer.draw();

      subHeadingNode.on('dblclick', () => {
        // create textarea over canvas with absolute position

        // first we need to find position for textarea
        // how to find it?

        // at first lets find position of text node relative to the stage:
        var textPosition = subHeadingNode.getAbsolutePosition();
        console.log(textPosition);

        // then lets find position of stage container on the page:
        var stageBox = stage.container().getBoundingClientRect();
        console.log(stageBox);

        // so position of textarea will be the sum of positions above:
        var areaPosition = {
          x: width + textPosition.x,
          y: height + textPosition.y
        };

        // create textarea and style it
        var textarea = document.createElement('textarea');
        document.body.appendChild(textarea);

        textarea.value = subHeadingNode.text();
        textarea.style.position = 'absolute';
        textarea.style.top = areaPosition.y + 'px';
        textarea.style.left = (areaPosition.x - 300) + 'px';
        textarea.style.width = subHeadingNode.width();

        textarea.focus();

        textarea.addEventListener('keydown', function (e) {
          // hide on enter
          if (e.keyCode === 13) {
            subHeadingNode.text(textarea.value);
            layer.draw();
            document.body.removeChild(textarea);
          }
        });
      });
}

function addBackground(){
//this code below is to apply random background.
let bgRect = new Konva.Rect({
            x:0,y:0,
            width: width,
            height: height,
            fill: colorArr[0],
            draggable: false,
        });
			
        bglayer.add(bgRect)
        bglayer.draw();
}

function returnRandom(){
        return Math.floor(Math.random() * 300) + 50 ;
}

function returnRandomY(){
        return Math.floor(Math.random() * 100) + 50 ;
}

function addShapes() {
        // I generate the d value in <path> from Blob.svelte's generateColor
        
        for(let i=0;i<5;i++)
        {
            let SVG = '<svg xmlns="http://www.w3.org/2000/svg"> <path fill="'+colorArr[i+1]+'" d="'+svgPaths[i]+'" transform="translate(100 100)" /> </svg>'    
            let url =  'data:image/svg+xml;base64,' + window.btoa(SVG);
            Konva.Image.fromURL(url, image => {
        image.draggable(false);
        image.x(returnRandom()+5);
        image.y(returnRandomY()+5);
        shapelayer.add(image);
        shapelayer.draw();
        })
        }
       /* const SVG = '<svg xmlns="http://www.w3.org/2000/svg"> <path fill="'+colorArr[1]+'" d="'+svgPaths[0]+'" transform="translate(100 100)" /> </svg>'
        const SVG1 = '<svg xmlns="http://www.w3.org/2000/svg"> <path fill="'+colorArr[2]+'" d="'+svgPaths[1]+'" transform="translate(100 100)" /> </svg>'
        const SVG2 = '<svg xmlns="http://www.w3.org/2000/svg"> <path fill="'+colorArr[3]+'" d="'+svgPaths[2]+'" transform="translate(100 100)" /> </svg>'
        const SVG3 = '<svg xmlns="http://www.w3.org/2000/svg"> <path fill="'+colorArr[4]+'" d="'+svgPaths[3]+'" transform="translate(100 100)" /> </svg>'
        
        const url =  'data:image/svg+xml;base64,' + window.btoa(SVG);
        const url1 =  'data:image/svg+xml;base64,' + window.btoa(SVG1);
        const url2 =  'data:image/svg+xml;base64,' + window.btoa(SVG2);
        const url3 =  'data:image/svg+xml;base64,' + window.btoa(SVG3);
        
        Konva.Image.fromURL(url, image => {
        image.draggable(true);
        image.x(returnRandom());
        image.y(returnRandomY());
        shapelayer.add(image);
        shapelayer.draw();
        })
       Konva.Image.fromURL(url1, image => {
        image.draggable(true);
        image.x(returnRandom());
        image.y(returnRandomY());
        shapelayer.add(image);
        shapelayer.draw();
        })
        Konva.Image.fromURL(url2, image => {
        image.draggable(true);
        image.x(returnRandom());
        image.y(returnRandomY());
        shapelayer.add(image);
        shapelayer.draw();
        })
        Konva.Image.fromURL(url3, image => {
        image.draggable(true);
        image.x(returnRandom());
        image.y(returnRandomY());
        shapelayer.add(image);
        shapelayer.draw();
        })*/
}

function forTwitter() {
		if (isMobile) {
            var dataURL = stage.toDataURL({  pixelRatio: 2 });
			downloadURI(dataURL, 'posterTW.png');
        }
        else {
            var dataURL = stage.toDataURL({  pixelRatio: 1 });
			downloadURI(dataURL, 'posterTW.png');
        }
        statsCounter();

}

function forFacebook() {
		if (isMobile) {
            var dataURL = stage.toDataURL({  pixelRatio: 4 });
			downloadURI(dataURL, 'posterFB.png');
        }
        else {
            var dataURL = stage.toDataURL({  pixelRatio: 2 });
			downloadURI(dataURL, 'posterFB.png');
        }
        statsCounter();

}

function forLinkedIn() {
    if (isMobile) {
            var dataURL = stage.toDataURL({  pixelRatio: 4 });
			downloadURI(dataURL, 'posterLN.png');
        }
        else {
            var dataURL = stage.toDataURL({  pixelRatio: 2 });
			downloadURI(dataURL, 'posterLN.png');
        }
        statsCounter();
}

function downloadURI(uri, name) {
        //part of funnel in firebase
        //firebaseAnalytics.logEvent('Downloaded_from_Quick_Mode');

        let link = document.createElement('a');
        link.download = name;
        link.href = uri;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      }

    beforeUpdate(() => {
	console.log("beforeUpdate was called");
		//setting default values to canvas dimensions
		width = 720;
		height = 350;
		// then create default one layer
        //layer = new Konva.Layer();
        
	});

  afterUpdate(() => {
    function fitStageIntoParentContainer() {
      let lcontainer = document.getElementById('stage-parent');

      // now we need to fit stage into parent
      var containerWidth = document.getElementById('stage-parent').offsetWidth;
      // to do this we need to scale the stage
      var scale = containerWidth / width;

      stage.width(width * scale);
      stage.height(height * scale);
      stage.scale({
        x: scale,
        y: scale
      });
      stage.draw();
    }

    fitStageIntoParentContainer();
    // adapt the stage on any window resize
    window.addEventListener('resize', fitStageIntoParentContainer);
  })

    onMount(() => {
        proAccessCheck();
        firebaseAnalytics.logEvent('blob_template');
        //code to intialize counter for stats
        db.collection('usersDB').doc(user).get().then(doc => {
            if (doc.exists) {
                let userData = doc.data();
                contentMadeSoFar = userData.totalCreated;
            } else {
                db.collection('usersDB').doc(user).set({
                            totalCreated: 0
                })
            }
            }).catch(function(error) {
                console.log("Error getting document:", error);
            });

        // first we need to create a stage
        stage = new Konva.Stage({
          container: 'konva-stage',   // id of container <div>
          width: 720,
          height: 350
        });

        layer = new Konva.Layer();
        bglayer = new Konva.Layer();
        shapelayer = new Konva.Layer();

        bglayer.moveToBottom();
        stage.add(bglayer);
        stage.add(shapelayer);
        stage.add(layer);

        addBackground();
        addFooterBar();
        addShapes();
        addText();
    });
</script>
<div>
    <Container fluid>
    {#if user}
	<Row class="pt-5">
		<Col sm="12" class="p-3">
			<h1 class="textBlue gradient-text font-weight-bold text-center">Blob Template</h1>
		</Col>
        <Col sm="12" class="text-center">
            <h4 class="font-weight-bold">Classic Design! Double click on text to edit it. Type something. Press Enter.</h4>
            <h6>*Blobs might not work on MacOS, try on Windows. Fix coming very soon.</h6>
		</Col>
    </Row>

    <Row>
      <Col class="d-flex justify-content-center">
        <div id="stage-parent">
          <div id="konva-stage"></div>
        </div>
      </Col>
    </Row>

        <Row class="mt-2 justify-content-center">
        <div>
            <Row class="pt-3">
              {#if !allowProAccess}
              <Col sm="12" class="text-center">
                  <h5 class="textBlue font-weight-bold">‚ö† This feature requires PRO account.</h5>
                  <button id="toggleForPro" class="pressDownDisabled btn-block btn-lg" disabled>Remix Design</button>
              </Col>
              {:else}
              <Col sm="12" class="text-center">
                <button id="toggleForPro" class="pressDown btn-block btn-lg" on:click={remix}>Remix Design</button>
              </Col>
              {/if}
            </Row>
                <div>
                    <Row class="pt-5">
                        <Col sm="12" class="text-center">
                            <h4>Download For Different Social Media.</h4>
                        </Col>
                    </Row>
                    <Row class="mt-2 mx-4 text-center">
                    <Col>
                    <button class="pressDown btn-block btn-lg" on:click={forFacebook}>Facebook</button>
                    </Col>
                    <Col>
                    <button class="pressDown btn-block btn-lg" on:click={forTwitter}>Twitter</button>
                    </Col>
                    <Col>
                    <button class="pressDown btn-block btn-lg" on:click={forLinkedIn}>Linkedin</button>
                    </Col>
                </Row>
                </div>
        </div>
        </Row>
        {:else}
        <NotFound />
        {/if}
        <hr/>
        <Footer />
        </Container>
        </div>