<svelte:head>
	<link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootswatch/4.5.0/flatly/bootstrap.min.css">
</svelte:head>
<style>
    Link {
        color:lime;
        text-decoration: none;
    }
	.textBlue {
		color: #5778f3;
	}
</style>
<script>
 let userSignedIn = false;
 import { auth, googleProvider, twitterProvider, db, firebaseAnalytics } from './firebase';
let authinfo = localStorage.getItem('usrkey');
let user = JSON.parse(authinfo);

 auth.onAuthStateChanged(function(user) {
  if (user) {
    userSignedIn = true;
  } else {
    userSignedIn = false;
  }
});


let allowProAccess = false;

function proAccessCheck() {
    db.collection('PROusers').doc(user).get().then(doc => {
        if (doc.exists) {
        allowProAccess = true;
    } else {
        // doc.data() will be undefined in this case
        allowProAccess = false;
    }
    }).catch(function(error) {
        console.log("Error:", error);
    });
}

  import { Router, Link,link, Route } from "svelte-routing";
  import {Row, Container, Button, Col,Collapse,Navbar,NavbarToggler,NavbarBrand,Nav,NavItem,NavLink,UncontrolledDropdown,DropdownToggle,DropdownMenu,DropdownItem} from 'sveltestrap';
  
  import { onMount } from 'svelte';

  import App from './App.svelte';
  import AppSumo from './AppSumo.svelte';
  import Home from './Home.svelte';
  import Login from './Login.svelte';
  import Template from './Template.svelte';
  import Changelog from './Changelog.svelte';
  import About from './About.svelte';
  import PrivacyPolicy from './PrivacyPolicy.svelte';
  import TOS from './TOS.svelte';
  import Support from './Support.svelte'
  import Difference from './Difference.svelte';
  import UseCases from './UseCases.svelte';
  import NotFound from './NotFound.svelte';
  
  //All the templates
  import Blob from './template/Blob.svelte';
  import Quotes from './template/Quotes.svelte';
  import Quotes2 from './template/Quotes-2.svelte';
  import Quotes3 from './template/Quotes-3.svelte';
  import Gradient from './template/Gradient.svelte';
  import Rect from './template/Rect.svelte';
  import Simp from './template/Simple.svelte';
  import Aesthetic from './template/Aesthetic.svelte';
  import HalfRect from './template/HalfRect.svelte';

  export let url = "";

  let isOpen = false;

function handleUpdate(event) {
  isOpen = event.detail.isOpen;
}

onMount(()=>{
    proAccessCheck();
})

</script>
<div>
    <!-- Navbar , logo code <img src="https://i.imgur.com/YqNaeaq.png" alt="logo" height="50" width="50" />-->
            <Router url="{url}">
                <Navbar class="navbar-expand-lg navbar-light">
                    <NavbarBrand href="/"><img src="https://i.imgur.com/YqNaeaq.png" alt="logo" height="70" width="70" /></NavbarBrand>
                    <NavbarToggler on:click={() => (isOpen = !isOpen)} />
                    <Collapse {isOpen} navbar expand="sm" on:update={handleUpdate}>
                        <Nav class="ml-auto" navbar>
                        <NavItem>
                            <a class="text-primary text-uppercase font-weight-bold nav-link" href="/Template">Templates</a>
                        </NavItem>
                        <NavItem>
                                <a class="text-primary text-uppercase font-weight-bold nav-link" href="/Changelog">Changelog</a>
                        </NavItem>
                        <NavItem>
                            <a class="text-primary text-uppercase font-weight-bold nav-link" href="/UseCases">Use Cases</a>
                        </NavItem>
                        <NavItem>
                            <a class="text-primary text-uppercase font-weight-bold nav-link" href="/#pricing">Pricing</a>
                        </NavItem>
                        {#if !allowProAccess}
                        <NavItem>
                            <a class="text-warning text-uppercase font-weight-bold nav-link" href="/Login#upgrade">Upgrade</a>
                        </NavItem>
                        {:else}
                        {/if}
                        {#if userSignedIn}
                        <NavItem>
                            <a class="text-uppercase font-weight-bold nav-link" href="/Login"><span class="textBlue">Dashboard</span></a>
                        </NavItem>
                        <NavItem>
                            <a href="#"  class="text-danger text-uppercase font-weight-bold nav-link" on:click={ () => {
                                auth.signOut()
                                localStorage.clear();
                                window.location = "/";
                            } }>Log out</a>
                        </NavItem>
                        {:else}
                        <NavItem>
                            <a class="text-uppercase font-weight-bold nav-link" href="/Login"><span class="textBlue">Sign In</span></a>
                        </NavItem>
                        {/if}

                        <!--<NavItem>
                            <a class="nav-link" href="/Support" use:link>Support</a>
                        </NavItem>
                        <UncontrolledDropdown>
                            <DropdownToggle nav caret>Legal</DropdownToggle>
                            <DropdownMenu class="bg-primary" right>
                            <DropdownItem><a class="nav-link" href="/PrivacyPolicy" use:link>Privacy Policy</a></DropdownItem>
                            <DropdownItem><a class="nav-link" href="/TOS" use:link>Terms</a></DropdownItem>
                            <DropdownItem><a class="nav-link" href="/About" use:link>About</a></DropdownItem>
                            </DropdownMenu>
                        </UncontrolledDropdown>
                        <NavItem>
                            <a href="/Login" replace><Button color="success">Create Design</Button></a>
                        </NavItem>-->
                        </Nav>
                    </Collapse>
                </Navbar>
                <div>
                    <Route path="/" component="{Home}" />
                    <Route path="/Changelog" component="{Changelog}" />
                    <Route path="/UseCases" component="{UseCases}" />
                    <Route path="/Template" component="{Template}" />
                    <Route path="/Login" component="{Login}" />
                    <Route path="/App" component="{App}" />
                    <Route path="/AppSumo" component="{AppSumo}" />
                    <Route path="/Support" component="{Support}" />
                    <Route path="/Difference" component="{Difference}" />
                    <Route path="/PrivacyPolicy" component="{PrivacyPolicy}" />
                    <Route path="/TOS" component="{TOS}" />
                    <Route path="/About" component="{About}" />
                    <Route path="/NotFound" component="{NotFound}" />

                    <Route path="/blob-design-template" component="{Blob}" />
                    <Route path="/quote-design-template" component="{Quotes}" />
                    <Route path="/quote-design-template-two" component="{Quotes2}" />
                    <Route path="/quote-design-template-three" component="{Quotes3}" />
                    <Route path="/gradient-design-template" component="{Gradient}"/>

                    <Route path="/rectangle-design-template" component="{Rect}"/>
                    <Route path="/rectangle-design-template-two" component="{HalfRect}"/>
                    
                    <Route path="/simple-design-template" component="{Simp}"/>
                    
                    <Route path="/aesthetic-design-template" component="{Aesthetic}"/>
                  </div>
            </Router>
        </div>
